      </svg>
      <div class="container">
        <div class="checkout-addr">
          <div class="page-title-normal">
            <h2 class="page-title-h2"><span>check out</span></h2>
          </div>
          <!-- process step -->
          <div class="check-step">
            <ul>
              <li class="cur"><span>Confirm</span> address</li>
              <li><span>View your</span> order</li>
              <li><span>Make</span> payment</li>
              <li><span>Order</span> confirmation</li>
            </ul>
          </div>

          <!-- address list -->
          <div class="page-title-normal checkout-title">
            <h2><span>Shipping address</span></h2>
          </div>
          <div class="addr-list-wrap">
            <div class="addr-list">
              <ul>
                <li
                  v-for="(val, index) in addressListFilter"
                  :key="index"
                  :class="{ check: checkIndex == index }"
                  @click="
                    checkIndex = index;
                    selectAddId = val.addressId;
                  "
                >
                  <dl>
                    <dt>{{ val.userName }}</dt>
                    <dd class="address">{{ val.streeName }}</dd>
                    <dd class="tel">{{ val.tel }}</dd>
                  </dl>
                  <div class="addr-opration addr-del">
                    <a
                      href="javascript:;"
                      class="addr-del-btn"
                      @click="delAddress(val.addressId)"
                    >
                      <svg class="icon icon-del">
                        <use xlink:href="#icon-del"></use>
                      </svg>
                    </a>
                  </div>
                  <div
                    class="addr-opration addr-set-default"
                    v-if="!val.isDefault"
                    @click="setDefault(val.addressId)"
                  >
                    <a href="javascript:;" class="addr-set-default-btn"
                      ><i>Set default</i></a
                    >
                  </div>
                  <div v-else class="addr-opration addr-default">
                    Default address
                  </div>
                </li>

                <li class="addr-new" @click="addAddress">
                  <div class="add-new-inner">
                    <i class="icon-add">
                      <svg class="icon icon-add">
                        <use xlink:href="#icon-add"></use>
                      </svg>
                    </i>
                    <p>Add new address</p>
                  </div>
                </li>
              </ul>
            </div>

            <div class="shipping-addr-more">
              <a
                class="addr-more-btn up-down-btn"
                :class="{ ' open': limit > 3 }"
                href="javascript:;"
                @click="expond"
              >
                more
                <i class="i-up-down">
                  <i class="i-up-down-l"></i>
                  <i class="i-up-down-r"></i>
                </i>
              </a>
            </div>
          </div>

          <!-- shipping method-->
          <div class="page-title-normal checkout-title">
            <h2><span>Shipping method</span></h2>
          </div>
          <div class="shipping-method-wrap">
            <div class="shipping-method">
              <ul>
                <li class="check">
                  <div class="name">Standard shipping</div>
                  <div class="price">Free</div>
                  <div class="shipping-tips">
                    <p>
                      Once shipped，Order should arrive in the destination in
                      1-7 business days
                    </p>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div class="next-btn-wrap">
            <router-link
              :to="{ path: '/orderConfrim', query: { addressId: selectAddId } }"
              class="btn btn--m btn--red"
              href="#"
              >Next</router-link
            >
          </div>
        </div>
      </div>
    </div>
    <shop-footer></shop-footer>
    <modal :mdShow="del" @update="update">
      <p slot="message">确定删除该地址吗?</p>
      <div slot="btn">
        <a class="btn btn--m" @click="sure">确定</a>
        <a class="btn btn--m" style="float: right" @click="del = false">取消</a>
      </div>
    </modal>
    <modal :mdShow="isAdd" @update="update" class="addAddress">
      <p slot="message">
        <label>添加您的收货地址!!!</label>
        <input type="text" placeholder="请输入姓名" v-model="name" />
        <input type="text" placeholder="请输入地址" v-model="address" />
        <input type="tel" placeholder="请输入电话" v-model="tel" />
        <input type="text" placeholder="请输入邮编" v-model="postCode" />
      </p>
      <div slot="btn">
        <a class="btn btn--m" @click="sureAdd">确定</a>
        <a class="btn btn--m" style="float: right" @click="isAdd = false"
          >取消</a
        >
      </div>
    </modal>
  </div>
</template>
<script>
import ShopHeader from "@/components/ShopHeader";
import ShopFooter from "@/components/ShopFooter";
import ShopBread from "@/components/ShopBread";
import Modal from "@/components/modal";
import axios from "axios";
export default {
  components: {
    ShopHeader,
    ShopFooter,
    ShopBread,
    Modal,
  },
  data() {
    return {
      addressList: [],
      checkIndex: 0,
      limit: 3, // 默认显示3条
      del: false,
      addressId: "",
      selectAddId: "", /// 选中的地址id
      isAdd: false, // 是否添加
      name: "",
      address: "",
      tel: "",
      postCode: "",
    };
  },
  computed: {
    addressListFilter() {
      return this.addressList.filter((val, index) => {
        return index < this.limit;
      });
    },
  },
  mounted() {
    this.getAddressList();
  },
  methods: {
    sureAdd() {
      var { name, address, tel, postCode } = this;

      axios
        .post("/users/addAddress", {
          userName: this.name,
          streetName: this.address,
          postCode: this.postCode,
          tel: tel,
          addressId: new Date().getTime(),
          isDefault: false,
        })
        .then((res) => {
          if (res.data.status == 0) {
            this.isAdd = false;
            this.getAddressList();
          }
        });
    },
    sure() {
      axios
        .post("/users/delAddress", { addressId: this.addressId })
        .then((res) => {
          // console.log(res);
          if (res.data.status == 0) {
            this.del = false;
            this.getAddressList();
          }
        });
    },
    update() {
      // 模态框关闭事件
      this.del = false;
      this.isAdd = false;
    },
    setDefault(id) {
      // console.log(id);
      axios.post("/users/setAddress", { addressId: id }).then((res) => {
        if (res.data.status == 0) {
          // console.log(res);
          this.getAddressList();
        }
      });
    },
    expond() {
      if (this.addressList.length > this.limit) {
        this.limit = this.addressList.length;
      } else {
        this.limit = 3;
      }
    },
    getAddressList() {
      axios.get("/users/addressList").then((res) => {
        this.addressList = res.data.result;
        this.selectAddId = this.addressList[0].addressId;
        // var defaultA = "";
        // res.data.result.forEach((val, index) => {
        //   if (val.isDefault) {
        //     defaultA = val;
        //     res.data.result.splice(index, 1);
        //     this.addressList = res.data.result;
        //     this.addressList.unshift(defaultA);
        //   }
        // });
        // this.selectAddId = this.addressList[0].addressId;
      });
    },
    addAddress() {
      this.isAdd = true;
    },
    delAddress(id) {
      this.del = true;
      this.addressId = id;
    },
  },
};
</script>
<style>
.addAddress input {
  margin-top: 10px;
  display: block;
  margin-left: 15px;
  height: 35px;
  width: 400px;
}
</style>

